  display: flex;
}

.logo {
  width: 100px;
}

.search-form {
  width: 100%;
  max-width: 350px;
}

.search-form input, .search-form button {
  float: left;
  border: 1px solid gray;
  padding: 5px;
  font-size: 16px;
}

.search-form input {
  width: 90%;
  background: #f1f1f1;
  border-radius: 5px 0 0 5px;
}

.search-form button {
  width: 10%;
  color: #fff;
  cursor: pointer;
  background: #2196f3;
  border-left: none;
  border-radius: 0 5px 5px 0;
}

.search-form button:hover {
  background: #0b7dda;
}

.search-form:after {
  content: "";
  clear: both;
  display: table;
}

.switch {
  width: 150px;
  height: 22px;
  font-size: 16px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 110px;
  right: 0;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 2px;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  transform: translateX(18px);
}

.slider.round {
  border-radius: 22px;
}

.slider.round:before {
  border-radius: 50%;
}

.gallery-container {
  padding-top: 0;
}

.images-info {
  text-align: right;
}

